<template>
  <div class="register-container">
    <div class="register">
      <div class="form" v-if="!emailControl">
        <div class="error-box" v-if="errorStatus">
          {{ errorText }}
        </div>
        <div class="input-box">
          <span>Full Name</span>
          <input type="text" required v-model="userData.fullName" />
        </div>
        <div class="input-box">
          <span>Your Email</span>
          <input type="email" required v-model="userData.email" />
        </div>
        <div class="input-box">
          <span>Your Phone</span>
          <input
            type="tel"
            required
            @keypress="isNumber($event)"
            v-model="userData.phone"
          />
        </div>
        <div class="input-box">
          <span>Password</span>
          <input type="password" required v-model="userData.password" />
        </div>
        <div class="button">
          <button @click="register">Register</button>
        </div>
        <div class="sign-info">
          <span class="dark-color d-inline-block line-height-2">
            Already Have Account ?
            <router-link to="/auth/sign-in1" class="iq-waves-effect pr-4">
                Sign in
              </router-link>
          </span>
        </div>
      </div>
      <div class="form email-control" v-else>
        <div
          class="error-box mail-control-error-message"
          v-if="errorMailControlStatus"
        >
          {{ errorText }}
        </div>
        <h4>Enter Confirmation Code</h4>
        <p>Enter the confirmation code we sent to {{ userData.email }}.</p>
        <div class="resend-code">
          <button
            @click="resendCode"
            :class="resendCodeSendCount === 1 ? 'disabled' : ''"
          >
            Resend Code
          </button>
          <span v-if="resendCodeSendCount === 1" class="count">{{
              count.minutes + ":" + count.seconds
            }}</span>
        </div>
        <div class="input-box">
          <span>Confirmation Code</span>
          <input type="tel" @keypress="isNumber($event)" v-model="emailControlUserToken" maxlength="6" required/>
        </div>
        <div class="button">
          <button class="turn-back" @click="emailControl = false">
            Turn Back
          </button>
          <button @click="mailControl">Approve</button>
        </div>
      </div>
    </div>
    <transition name="fade">
      <div class="resendCodeWarning" v-if="resendCodeStatus">
        {{ resendCodeMessage }}
      </div>
    </transition>
  </div>
</template>

<script>
import cryptoJS from 'crypto-js'
import api from '@/router/api'
import Axios from 'axios'
export default {
  name: 'Register',
  data () {
    return {
      emailControl: false,
      emailToken: '',
      emailControlUserToken: '',
      resendCodeSendCount: 0,
      resendCodeStatus: false,
      resendCodeMessage: '',
      userData: {
        fullName: '',
        email: '',
        phone: '',
        password: ''
      },
      errorMailControlStatus: false,
      errorStatus: false,
      errorText: 'Please fill in all the blanks!',
      count: {
        seconds: 59,
        minutes: '01'
      }
    }
  },
  methods: {
    register () {
      if (
        this.userData.fullName.length > 2 &&
        this.userData.email.length > 2 &&
        this.userData.phone.length > 2 &&
        this.userData.password.length > 4
      ) {
        const reg = /^(([^<>()\]\\.,;:\s@"]+(\.[^<>()\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/
        if (reg.test(this.userData.email)) {
          this.errorStatus = false
          this.emailToken = ''
          for (let i = 1; i <= 6; i++) {
            this.emailToken =
              this.emailToken + String(Math.floor(Math.random() * 10))
          }
          const key = cryptoJS.AES.encrypt(
            this.emailToken,
            this.userData.email.slice(0, 7)
          ).toString()
          if (this.resendCodeSendCount < 1) {
            api
              .post('/users/register/mail', {
                userEmail: this.userData.email,
                phone: this.userData.phone,
                authCode: key
              })
              .then((res) => {
                if (Number(res.data.status) === 1) {
                  this.resendCodeSendCount += 1
                  this.minuteCount()
                  this.emailControl = true
                  setTimeout(() => {
                    this.resendCodeSendCount = 0
                  }, 20000 * 6)
                } else if (Number(res.data.status) === 0) {
                  this.errorText =
                    'There is already a registered user by this email or phone!'
                  this.errorStatus = true
                  setTimeout(() => {
                    this.errorStatus = false
                  }, 5000)
                } else {
                  this.errorText = 'Something went wrong. Please try again.'
                  this.errorStatus = true
                  setTimeout(() => {
                    this.errorStatus = false
                  }, 5000)
                }
              })
              .catch(() => {
                this.errorText =
                  'An error has occurred, please try again or contact us!'
                this.errorStatus = true
                setTimeout(() => {
                  this.errorStatus = false
                }, 5000)
              })
          } else {
            this.emailControl = true
          }
        } else {
          this.errorText = 'Enter a valid email address.'
          this.errorStatus = true
          setTimeout(() => {
            this.errorStatus = false
          }, 5000)
        }
      } else {
        this.errorText = 'Please fill in all the blanks!'
        this.errorStatus = true
        setTimeout(() => {
          this.errorStatus = false
        }, 5000)
      }
    },
    mailControl () {
      if (this.emailControlUserToken.length > 0) {
        if (this.emailToken === this.emailControlUserToken) {
          this.errorMailControlStatus = false
          this.emailControlUserToken = ''
          api
            .post('/users/register', {
              fullName: this.userData.fullName,
              email: this.userData.email,
              phone: this.userData.phone,
              password: this.userData.password
            })
            .then((res) => {
              if (Number(res.data.status) === 1) {
                let text = this.userData.fullName
                const trMap = {
                  çÇ: 'c',
                  ğĞ: 'g',
                  şŞ: 's',
                  üÜ: 'u',
                  ıİ: 'i',
                  öÖ: 'o'
                }
                for (var key in trMap) {
                  text = text.replace(new RegExp('[' + key + ']', 'g'), trMap[key])
                }
                const fullName = text.replace(/[^-a-zA-Z0-9\s]+/ig, '').replace(/\s/gi, '').replace(/[-]+/gi, '-').toLowerCase()
                const mainPassword = this.randomPassword(11, 'aA0')
                const investPassword = this.randomPassword(11, 'aA0')
                const phonePassword = this.randomPassword(11, 'aA0')
                Axios.post(`https://api.launchfxm.com/api_v1/create_user?Name=${fullName}&Leverage=200&Group=test/APITEST&MainPassword=${mainPassword}&InvestPassword=${investPassword}&PhonePassword=${phonePassword}`)
              } else {
                this.errorText = 'Something went wrong. Please try again.'
                this.errorStatus = true
                setTimeout(() => {
                  this.errorStatus = false
                }, 5000)
              }
            })
            .catch(() => {
              this.errorText = 'Something went wrong. Please try again.'
              this.errorStatus = true
              setTimeout(() => {
                this.errorStatus = false
              }, 5000)
            })
        } else {
          var emailControlTokenTimeOut
          clearTimeout(emailControlTokenTimeOut)
          this.errorText =
            'This code is not valid. You can request a new code.'
          this.errorMailControlStatus = true
          this.emailControlUserToken = ''
          emailControlTokenTimeOut = setTimeout(() => {
            this.errorMailControlStatus = false
          }, 5000)
        }
      } else {
        this.errorText = 'Please fill in all the blanks!'
        this.errorMailControlStatus = true
        setTimeout(() => {
          this.errorMailControlStatus = false
        }, 5000)
      }
    },
    randomPassword (uzunluk, semboller) {
      let maske = ''
      if (semboller.indexOf('a') > -1) maske += 'qwertyuopasdfghjklizxcvbnm'
      if (semboller.indexOf('A') > -1) maske += 'QWERTYUOPASDFGHJKLIZXCVBNM'
      if (semboller.indexOf('0') > -1) maske += '0123456789'
      let sonuc = ''
      for (var i = uzunluk; i > 0; --i) {
        sonuc += maske[Math.floor(Math.random() * maske.length)]
      }
      return sonuc
    },
    resendCode () {
      if (this.resendCodeSendCount < 1) {
        this.emailToken = ''
        for (let i = 1; i <= 6; i++) {
          this.emailToken =
            this.emailToken + String(Math.floor(Math.random() * 10))
        }
        const key = cryptoJS.AES.encrypt(
          this.emailToken,
          this.userData.email.slice(0, 7)
        ).toString()
        api
          .post('/users/register/mail', {
            userEmail: this.userData.email,
            phone: this.userData.phone,
            authCode: key
          })
          .then((res) => {
            if (res.data.status === 1) {
              this.resendCodeSendCount += 1
              this.minuteCount()
              this.resendCodeMessage =
                'Your confirmation code has been sent to the e-mail address you specified!'
              this.resendCodeStatus = true
              this.emailControl = true
              setTimeout(() => {
                this.resendCodeSendCount = 0
              }, 20000 * 6)
              setTimeout(() => {
                this.resendCodeStatus = false
              }, 5000)
            }
          })
          .catch(() => {
            this.errorText =
              'An error has occurred, please try again or contact us!'
            this.errorStatus = true
            setTimeout(() => {
              this.errorStatus = false
            }, 5000)
          })
      }
    },
    minuteCount () {
      this.count.seconds = 59
      this.count.minutes = '01'
      const countInterval = setInterval(() => {
        this.count.seconds -= 1
        // eslint-disable-next-line no-unused-expressions
        this.count.seconds < 10
          ? (this.count.seconds = '0' + this.count.seconds)
          : this.count.seconds
        if (this.count.minutes > 0 && this.count.seconds < 1) {
          this.count.seconds = 59
          this.count.minutes = '00'
        }
        if (this.count.minutes === 0 && this.count.seconds === 0) {
          clearInterval(countInterval)
        }
      }, 950)
    },
    isNumber: function (evt) {
      evt = evt || window.event
      const charCode = evt.which ? evt.which : evt.keyCode
      if (
        charCode > 31 &&
        (charCode < 48 || charCode > 57) &&
        charCode !== 46
      ) {
        evt.preventDefault()
      } else {
        return true
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.mail-control-error-message {
  margin-bottom: 15px;
}
.register-container {
  width: 100%;
  display: flex;
  .register {
    height: 100%;
    flex: 1;
    display: grid;
    place-content: center;
    .form {
      .login-link a {
        color: #ff7130;
      }
      .input-box {
        margin: 20px 0;
        span {
          display: block;
          margin-bottom: 3px;
          font-size: 17px;
        }
        input {
          outline: none;
          background: none;
          width: 400px;
          height: 40px;
          border: 1px solid rgba(#ccc, 0.5);
          border-left: 4px solid #cccccc;
          font-size: 17px;
          border-radius: 2px;
          padding-left: 5px;
          transition: 300ms;
          &:focus,
          &:valid {
            border-left-color: #ff7130;
          }
        }
      }
      .button {
        text-align: right;
        button {
          cursor: pointer;
          outline: none;
          border: 2px solid transparent;
          background: #ff7130;
          padding: 10px 30px;
          color: #ffffff;
          font-size: 20px;
          font-weight: 500;
          font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
          Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
          border-radius: 2px;
          transition: 300ms;
          &:hover {
            border-color: #ff7130;
            color: #ff7130;
            background: transparent;
          }
        }
      }
    }
    .email-control {
      h4 {
        font-size: 20px;
        font-weight: 500;
        text-align: center;
      }
      p {
        font-size: 15px;
        color: darken(#fff, 27%);
        margin: 8px 0;
      }
      .resend-code {
        text-align: center;
        button {
          background: none;
          border: none;
          outline: none;
          color: #ff7130;
          cursor: pointer;
          font-size: 16px;
        }
        button.disabled {
          opacity: 0.5;
        }
        .count {
          margin-left: 10px;
          font-size: 15px;
          color: rgba(black, 0.7);
        }
      }
      .input-box input {
        width: 100%;
      }
      .button {
        display: flex;
        justify-content: space-between;
        button {
          padding: 5px 15px;
        }
        .turn-back {
          border: none;
          background: none;
          font-size: 17px;
          color: #ff7130;
          padding: 0;
        }
      }
    }
  }
  .image {
    height: 100%;
    flex: 1;
    background: #ff7130;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.resendCodeWarning {
  position: fixed;
  width: 100%;
  padding: 10px 0;
  background: black;
  color: white;
  bottom: 0;
  text-align: center;
}
@media (max-width: 1320px) {
  .register-container {
    height: max-content !important;
    flex-direction: column-reverse;
    .image {
      overflow: hidden;
      height: 350px;
      padding: 15px 0;
      svg {
        height: 100%;
        width: 100%;
      }
    }
    .register {
      padding: 15px 0 50px 0;
    }
  }
}
@media (max-width: 450px) {
  .register-container {
    .image {
      padding: 20px;
      margin: 20px 0;
    }
    .register {
      .form {
        width: 100vw;
        padding: 15px 25px 0 25px;
        .input-box input {
          width: 100% !important;
        }
        .button button {
          padding: 5px 15px;
        }
      }
    }
  }
}

.fade-enter-active {
  transition: all 0.3s;
}
.fade-leave-active {
  transition: all 0.3s;
}
.fade-enter,
.fade-leave-to {
  transform: translateY(20px);
  opacity: 0;
}
</style>
